.w-select {
  display: -webkit-inline-box;
  display: inline-flex;
  font-size: 14px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  cursor: pointer;
  color: #182026;
  padding: 6px 25px 6px 10px;
  width: 100%;
  max-width: 100%;
  margin: 0;
  line-height: 18px;
  vertical-align: middle;
  box-sizing: border-box;
  border: none;
  box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
  border-radius: 3px;
  background-color: #f5f8fa;
  background-image: url('data:image/svg+xml;base64,'), -webkit-gradient(linear, left top, left bottom, from(hsla(0, 0%, 100%, 0.8)), to(hsla(0, 0%, 100%, 0)));
  background-image: url('data:image/svg+xml;base64,'), linear-gradient(180deg, hsla(0, 0%, 100%, 0.8), hsla(0, 0%, 100%, 0));
  background-repeat: no-repeat, repeat;
  background-position: right 0.7em top 50%, 0 0;
  background-size: 0.62em auto, 100%;
}
.w-select:focus {
  outline: none !important;
}
.w-select:hover {
  box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
  background-clip: padding-box;
  background-color: #ebf1f5;
}
.w-select:disabled {
  box-shadow: none;
  background-image: url('data:image/svg+xml;base64,');
  background-size: 0.52em auto, 100%;
  background-color: #DDDDDD;
  opacity: 0.75;
  color: #A5A5A5;
  cursor: not-allowed;
  resize: none;
}
.w-select-large {
  padding: 9px 25px 9px 12px;
  font-size: 16px;
}
.w-select-small {
  padding: 3px 25px 3px 7px;
  font-size: 12px;
}
